import React, { useState } from 'react'
import { Toolbar, AppBar, Slide, useScrollTrigger } from '@mui/material'
import { NavLink } from 'react-router-dom'
import useStyles from './DiaryHeader.style'

interface Props {
  window?: () => Window
  children: React.ReactElement
}

function HideOnScroll(props: Props) {
  const { children, window } = props
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
  })

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  )
}

const DiaryHeader = () => {
  const classes = useStyles()

  return (
    <HideOnScroll>
      <AppBar className={classes.appBar} position="fixed">
        <Toolbar className={classes.toolbar} sx={{ ml: '160px ' }}>
          <div className={classes.linkContainer}>
            <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/">
              
            </NavLink>
            <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/metrics">
              
            </NavLink>
            <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/feed">
              
            </NavLink>
            <NavLink className={({ isActive }) => `${classes.link} ${isActive && classes.link_active}`} to="/contacts">
              
            </NavLink>
          </div>
        </Toolbar>
      </AppBar>
    </HideOnScroll>
  )
}

export default React.memo(DiaryHeader)
