import React from 'react'
import { useSnackbar, SnackbarKey } from 'notistack'
import { useAppSelector, useAppDispatch } from '../services/store/hooks'
import { removeSnackbar, closeSnackbar as closeSnackbarAction } from '../services/slices/app-slice'
import AchievementSnackBar from '../components/Sub/snackbars/AchievementSnackBar/AchievementSnackBar'
import { NotificationSnackbar } from '../components/Sub'

let displayed: Array<SnackbarKey> = []

const useNotifier = () => {
  const dispatch = useAppDispatch()
  const notifications = useAppSelector((store) => store.app.achievementNotifications || [])
  const { enqueueSnackbar, closeSnackbar } = useSnackbar()
  const storeDisplayed = (id: SnackbarKey) => {
    displayed = [...displayed, id]
  }

  const removeDisplayed = (id: SnackbarKey) => {
    displayed = [...displayed.filter((key) => id !== key)]
  }

  React.useEffect(() => {
    notifications.forEach(({ key, message, info = {}, options = {}, dismissed = false }) => {
      if (dismissed) {
        closeSnackbar(key)
        dispatch(closeSnackbarAction(key))
        return
      }
      if (displayed.includes(key)) return
      const content = (contentKey: string) =>
        info.type === 'achievement' ? (
          <AchievementSnackBar
            id={contentKey}
            title={info.title || ''}
            message={info.message || ''}
            image={info.image || ''}
          />
        ) : (
          <NotificationSnackbar id={contentKey} header={info.title || ''} message={info.message || ''} />
        )

      enqueueSnackbar(message, {
        key,
        ...options,
        content,
        onClose: (event, reason, myKey) => {
          dispatch(removeSnackbar(myKey))
        },
        onExited: (event, myKey) => {
          dispatch(removeSnackbar(myKey))
          removeDisplayed(myKey)
        },
      })
      storeDisplayed(key)
    })
  }, [notifications, closeSnackbar, enqueueSnackbar, dispatch])
}

export default useNotifier
