const drawerWidth = 160

type RouterLinkProps = React.PropsWithChildren<{
  to: string
  text: string
  icon: React.ReactElement
  disabled?: boolean
}>

const RouterLink = ({ to, text, icon, disabled }: RouterLinkProps) => {
  type MyNavLinkProps = Omit<NavLinkProps, 'to'>
  const MyNavLink = React.useMemo(
    () =>
      React.forwardRef<HTMLAnchorElement, MyNavLinkProps>((navLinkProps, ref) => {
        const { className: previousClasses, ...rest } = navLinkProps
        const elementClasses = previousClasses?.toString() ?? ''
        const classes = disabled ? `${elementClasses} disabled` : elementClasses
        const diaryPaths = ['/', '/diary', '/contacts', '/metrics', '/feed']
        let isDiaryPath = false
        const location = useLocation()
        if (to === '/' && diaryPaths.includes(location.pathname)) {
          isDiaryPath = true
        }
        const handleClick = (e: React.MouseEvent) => {
          if (disabled) e.preventDefault()
        }
        return (
          <NavLink
            {...rest}
            ref={ref}
            to={to}
            end
            className={({ isActive }) => (isActive || isDiaryPath ? `${classes} active` : classes)}
            onClick={handleClick}
          />
        )
      }),
    [to, disabled],
  )
  return (
    <ListItem>
      <ListItemButton component={MyNavLink}>
        <ListItemIcon>{icon}</ListItemIcon>
        <ListItemText primary={text} />
      </ListItemButton>
    </ListItem>
  )
}

interface MenuItemProps {
  text: string
  icon: React.ReactElement
  onClick: () => void
}

const MenuItem = ({ text, icon, onClick }: MenuItemProps) => (
  <ListItem>
    <ListItemButton onClick={onClick}>
      <ListItemIcon>{icon}</ListItemIcon>
      <ListItemText primary={text} />
    </ListItemButton>
  </ListItem>
)

