import React, { useEffect } from 'react'
import { Outlet } from 'react-router-dom'
import { Box, CssBaseline, Toolbar } from '@mui/material'
import DiaryHeader from './DiaryHeader/DiaryHeader'
import { CustomSnackBar } from '../../Sub'
import SidebarMenu from '../../Sub/SidebarMenu/SidebarMenu'
import useNotifier from '../../../hooks/useNotifier'
import { useAppDispatch } from '../../../services/store/hooks'
import { closeSocket, openSocket } from '../../../services/slices/websocket-slice'

function DiaryLayout({ snack, setSnack }) {
  const dispatch = useAppDispatch()

  useNotifier()

  useEffect(() => {
    dispatch(openSocket())
    return () => {
      dispatch(closeSocket())
    }
  }, [])

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <CustomSnackBar onClose={() => setSnack({ ...snack, open: false })} {...snack} />
      <DiaryHeader />
      <SidebarMenu setSnack={setSnack} />
      <Box component="main" sx={{ flexGrow: 1 }}>
        <Toolbar sx={{ '&.MuiToolbar-root': { minHeight: 60 } }} />
        <Outlet />
      </Box>
    </Box>
  )
}

export default React.memo(DiaryLayout)
